@charset "utf-8" ;

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td,
menu {
    margin: 0px;
    padding: 0px;
}
*:focus {
    outline:none;
    -webkit-tap-highlight-color: transparent;
}
li {
    list-style:none;
}
a {
    text-decoration: none;
}
i{
    font-style: normal;
}



button{
    background:none;
    border:none;
}
textarea {
    resize:none;
}
select {
    margin: 0;
    outline-offset: 0;
    outline-style: none;
    outline-width: 0;
    -webkit-font-smoothing: inherit;
    background-image: none ;
}
body {
  font-family:-apple-system-font,Helvetica Neue,Helvetica,sans-serif ;
}



